.footer-container{
    position: relative;
    display: flex;
    min-height: 64px;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0px 10%;
    bottom: 0px;
    flex-wrap: wrap;
}
.footer-container h3{
    margin: 15px 0px;
}
.socialmenu{
    margin: 15px 0px;
}
.footer-container p {
    font-size: 14px;
}

.socialicon{
    color: inherit;
    margin: 5px;
}